import React from "react";
import { styled, Typography, Button } from "@mui/material";

const MainPictureDiv = styled("div")(({ theme }) => ({
  paddingLeft: 60,
  height: "100%",
  width: "50%",
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
  backgroundImage: 'url("//unsplash.it/701/700")',
  backgroundRepeat: "no-repeat",
  "@media (max-width: 650px)": {
    height: "100vh",
    width: "100%",
  },
}));

const MakeButtonDown = styled("div")(({ theme }) => ({
  position: "absolute",
  bottom: 40,
  right: 75,
}));

const Details = (props) => {
  const [disabledButton, setDisabledButton] = React.useState(false);
  const [availability, setavailibility] = React.useState("Check Availability");
  const availabilityHandler = () => {
    availability === "Check Availability"
      ? setavailibility("Available")
      : setavailibility("Available");
  };
  return (
    <MainPictureDiv>
      <Typography variant="h3" component="div" gutterBottom>
        Details
      </Typography>
      <br />
      {console.log(props)}
      <Typography variant="h4" component="div" gutterBottom>
        Medicine Name: {props.properties.name}
      </Typography>
      Description: Lorem ipsum dolor sit amet consectetur adipisicing elit.
      <br />
      Ratione facere nobis vero tenetur nam. Temporibus voluptates enim et quis
      cumque esse eius asperiores omnis in suscipit,
      <MakeButtonDown>
        <Button
          variant="contained"
          color={availability === "Available" ? "success" : "primary"}
          disabled={disabledButton}
          onClick={() => {
            availabilityHandler();
            //it'll never run, just to remove warning
            availability === ""
              ? setDisabledButton(false)
              : setDisabledButton(false);
          }}
        >
          {availability}
        </Button>
      </MakeButtonDown>
    </MainPictureDiv>
  );
};

export default Details;
