import React from "react";
import { styled } from "@mui/material/styles";
import Typography from "@mui/material/Typography";
import { Box } from "@mui/material";

const IntroImage = styled("div")(({ theme }) => ({
  height: 400,
  minWidth: 400,
  borderRadius: "50%",
  backgroundRepeat: "no-repeat",
  backgroundImage: 'url("//unsplash.it/401/400")',
}));

const MakePhotoASide = styled("div")(({ theme }) => ({
  height: 800,
  width: "100%",
  display: "flex",
  alignItems: "center",
  justifyContent: "space-between",
}));

const SplitInLines = styled("div")(({ theme }) => ({
  display: "flex",
  minWidth: "40%",
  flexDirection: "column",
}));

const PartTwo = () => {
  return (
    <Box sx={{ width: "100%", maxWidth: "85vw" }}>
      <MakePhotoASide>
        <IntroImage />
        <SplitInLines>
          <Typography variant="h1">Explore..</Typography>
          <br />
          <Typography variant="h1">Find..</Typography>
          <br />
          <Typography variant="h1">Order..</Typography>
          <br />
        </SplitInLines>
      </MakePhotoASide>
    </Box>
  );
};

export default PartTwo;
