import { styled } from "@mui/material/styles";
import PartOne from "./PartOne";
import PartTwo from "./PartTwo";
import PartThree from "./PartThree";

const MainDiv = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  marginTop: 50,
  marginBottom: 50,
}));

const Home = () => {
  return (
    <>
      <MainDiv>
        <PartOne />
      </MainDiv>
      <hr />
      <MainDiv>
        <PartTwo />
      </MainDiv>
      <hr />
      <MainDiv>
        <PartThree />
      </MainDiv>
    </>
  );
};

export default Home;
