import React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { styled } from "@mui/material/styles";
import data from "../data/data.json";
import { Link } from "react-router-dom";

const ImgMediaCard = (props) => {
  return (
    <Card
      sx={{
        "@keyframes growit": {
          "100%": "scale(1.2)",
        },
        transition: "transform .2s",
        maxWidth: 300,
        marginTop: 5,
        marginBottom: 5,
        "&:hover": {
          //animation: "growit 300ms",
          transform: "scale(1.125)",
        },
      }}
    >
      <CardMedia
        component="img"
        alt="green iguana"
        height="100"
        image={`//unsplash.it/${props.height}/${props.width}`}
      />
      <CardContent>
        <Typography gutterBottom variant="h5" component="div">
          {props.name}
        </Typography>
        <Typography variant="body2" color="text.secondary">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Ipsam
          aperiam, omnis dolorem molestias ut dolor aspernatur fugiat
          consequatur consequuntur excepturi at quibusdam totam labore
          necessitatibus veritatis accusamus voluptatum architecto! Odit sint
          mollitia perferendis praesentium?
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Share</Button>
        <Button
          size="small"
          component={Link}
          to={{
            pathname: `/product?${props.name}`,
          }}
          state={{
            name: `${props.name}`,
            height: `${props.height}`,
            width: `${props.width}`,
          }}
        >
          Learn More
        </Button>
      </CardActions>
    </Card>
  );
};

const ProductsMediaData = data[1].ProductsMediaData;

const AdjustMediaData = styled("div")(({ theme }) => ({
  marginTop: "175px",
  marginLeft: 100,
  display: "grid",
  gridGap: "2vh",
  alignItems: "center",
  justifyContent: "center",
  gridTemplateColumns: "repeat(4, 1fr)",
  "@media (max-width: 650px)": {
    gridTemplateColumns: "repeat(1, 1fr)",
  },
}));

const Products = () => {
  return (
    <AdjustMediaData>
      {ProductsMediaData.map((ele, index) => {
        return (
          <ImgMediaCard
            key={ele.name}
            name={ele.name}
            height={ele.height}
            width={ele.width}
            //price={ele.price}
          />
        );
      })}
    </AdjustMediaData>
  );
};

export default Products;
