import React from "react";

import { styled } from "@mui/material";

const MainPictureDiv = styled("div")(({ theme }) => ({
  height: "100%",
  width: "50%",
  display: "flex",
  flexDirection: "row",
  alignItems: "center",
  justifyContent: "center",
  backgroundImage: 'url("//unsplash.it/700/700")',
  backgroundRepeat: "no-repeat",
  "@media (max-width: 650px)": {
    height: "100vh",
    width: "100%",
  },
}));

const Picture = () => {
  return <MainPictureDiv>Picture</MainPictureDiv>;
};

export default Picture;
