import { Box } from "@mui/material";
import { styled } from "@mui/material/styles";
import Typography from "@mui/material/Typography";

const MainDiv = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  marginTop: 175, //marginLeft: 80,
}));

const IntroImage = styled("div")(({ theme }) => ({
  height: 400,
  minWidth: 400,
  borderRadius: "50%",
  backgroundRepeat: "no-repeat",
  backgroundImage: 'url("//unsplash.it/400/400")',
}));

const MakePhotoASide = styled("div")(({ theme }) => ({
  height: 400,
  width: "100%",
  display: "flex",
  justifyContent: "flex-end",
}));

const PartOne = () => {
  return (
    <MainDiv>
      <Box sx={{ width: "100%", maxWidth: "85vw" }}>
        <Typography variant="h1" component="div" gutterBottom>
          Welcome to ##__WEBSITE__NAME__##
        </Typography>
        <hr />
        <Typography variant="body1" gutterBottom>
          Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quos
          blanditiis tenetur unde suscipit, quam beatae rerum inventore
          consectetur, neque doloribus, cupiditate numquam dignissimos laborum
          fugiat deleniti? Eum quasi quidem quibusdam.
        </Typography>
        <hr />
        <MakePhotoASide>
          <Typography variant="h1">Locate to us for your meds.</Typography>
          <IntroImage />
        </MakePhotoASide>
      </Box>
    </MainDiv>
  );
};

export default PartOne;
