import React from "react";
import { styled } from "@mui/material/styles";
import InstagramIcon from "@mui/icons-material/Instagram";
import { Box, Typography } from "@mui/material";

const CenterIt = styled("div")(({ theme }) => ({
  height: "100vh",
  width: "100vw",
  display: "flex",
  flexDirection: "row",
  alignItems: "center",
  justifyContent: "space-between",
  "@media (max-width: 650px)": {
    marginTop: 150,
    marginLeft: 30,
    flexDirection: "column",
  },
}));

const Gottem = styled("iframe")(({ theme }) => ({
  height: "100vh",
  width: "50%",
  "@media (max-width: 650px)": {
    height: "50vh",
    width: `calc(100% - 50px)`,
  },
}));

const Contact = () => {
  return (
    <>
      <CenterIt>
        <Gottem
          title="map"
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d4126.925645814859!2d80.34828618782832!3d26.455567563766557!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x399c475f92e2f43d%3A0x3e93915f4bc55768!2sKanpur%20central%20railway%20station!5e0!3m2!1sen!2sin!4v1643385424405!5m2!1sen!2sin"
          loading="lazy"
        ></Gottem>
        <Box
          sx={{
            width: "50%",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Typography
            variant="h1"
            sx={{
              padding: 12,
              "@media (max-width: 650px)": {
                fontSize: 20,
              },
            }}
          >
            Follow me on Instagram!!
          </Typography>
          <a href="https://instagram.com/triv.tony" style={{ padding: 10 }}>
            <InstagramIcon />
            My Insta
          </a>
        </Box>
      </CenterIt>
    </>
  );
};

export default Contact;
