import * as React from "react";
import { styled, useTheme } from "@mui/material/styles";
import Box from "@mui/material/Box";
import MuiDrawer from "@mui/material/Drawer";
import MuiAppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import List from "@mui/material/List";
import CssBaseline from "@mui/material/CssBaseline";
import Typography from "@mui/material/Typography";
import Divider from "@mui/material/Divider";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import InboxIcon from "@mui/icons-material/MoveToInbox";
import MailIcon from "@mui/icons-material/Mail";
import { Link } from "react-router-dom";
import LoginIcon from "@mui/icons-material/Login";
import AddIcon from "@mui/icons-material/Add";
import {
  HomeTwoTone,
  NotesTwoTone,
  PhoneAndroidTwoTone,
  ShoppingBagTwoTone,
} from "@mui/icons-material";
import { Button } from "@mui/material";

const drawerWidth = 240;
const NavIcons = [
  <HomeTwoTone />,
  <ShoppingBagTwoTone />,
  <NotesTwoTone />,
  <PhoneAndroidTwoTone />,
];

const MakeButtonDown = styled("div")(({ theme, open }) => ({
  position: "absolute",
  display: "block",
  overflowX: "hidden",
  width: "100%",
  bottom: 20,
  right: open ? 40 : 7,
}));

const openedMixin = (theme) => ({
  width: drawerWidth,
  transition: theme.transitions.create("width", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen,
  }),
  overflowX: "hidden",
});

const closedMixin = (theme) => ({
  transition: theme.transitions.create("width", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  overflowX: "hidden",
  width: `calc(${theme.spacing(7)} + 1px)`,
  [theme.breakpoints.up("sm")]: {
    width: `calc(${theme.spacing(9)} + 1px)`,
  },
});

const DrawerHeader = styled("div")(({ theme }) => ({
  display: "flex",
  height: "120px",
  alignItems: "center",
  justifyContent: "flex-end",
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
}));

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  display: "flex",
  height: "120px",
  alignItems: "stretch",
  justifyContent: "center",
  backgroundColor: "black",
  transition: theme.transitions.create(["width", "margin"], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  width: drawerWidth,
  flexShrink: 0,
  whiteSpace: "nowrap",
  boxSizing: "border-box",
  ...(open && {
    ...openedMixin(theme),
    "& .MuiDrawer-paper": openedMixin(theme),
  }),
  ...(!open && {
    ...closedMixin(theme),
    "& .MuiDrawer-paper": closedMixin(theme),
  }),
}));

export default function MiniDrawer() {
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar position="fixed" open={open}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            sx={{
              marginRight: "36px",
              ...(open && { display: "none" }),
            }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap component="div">
            Dava India Pvt. Ltd.
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer variant="permanent" open={open}>
        <DrawerHeader>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === "rtl" ? (
              <ChevronRightIcon />
            ) : (
              <ChevronLeftIcon />
            )}
          </IconButton>
        </DrawerHeader>
        <Divider />
        <List>
          {["Home", "Products", "About", "Contact Us"].map((text, index) => {
            return (
              <ListItem button key={text} sx={{ paddingLeft: 2 }}>
                <Button
                  component={Link}
                  to={
                    text === "Home"
                      ? "/"
                      : text.replace(" ", "").toLocaleLowerCase()
                  }
                  sx={{
                    height: "100%",
                    width: "100%",
                    color: "inherit",
                  }}
                >
                  <ListItemIcon>{NavIcons[index]}</ListItemIcon>
                  <ListItemText primary={text} />
                </Button>
              </ListItem>
            );
          })}
        </List>
        <Divider />
        <List>
          {["All mail", "Notifications"].map((text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List>
        <MakeButtonDown>
          <Button
            component={Link}
            to="/login"
            sx={{
              width: "100%",
              height: "120%",
              alignItems: "center",
              justifyContent: "space-evenly",
              color: open ? "inherit" : "inherit",
              display: open ? "flex" : "flex",
            }}
            onClick={() => {
              console.log("works");
            }}
          >
            <LoginIcon />
            <Typography
              sx={{
                display: open ? "block" : "none",
              }}
            >
              Login
            </Typography>
          </Button>
          <Button
            component={Link}
            to="/login"
            sx={{
              width: "100%",
              height: "120%",
              alignItems: "center",
              justifyContent: "space-evenly",
              color: open ? "inherit" : "inherit",
              display: open ? "flex" : "flex",
            }}
            onClick={() => {
              console.log("works");
            }}
          >
            <AddIcon />
            <Typography
              sx={{
                display: open ? "block" : "none",
              }}
            >
              SignUp
            </Typography>
          </Button>
        </MakeButtonDown>
      </Drawer>
    </Box>
  );
}
